/* links
	----------------------------------- */
.link-decor {
	color: #008CFF;
	span {
		padding-bottom: 3px;
		background-image: linear-gradient(to right, #008CFF 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #008CFF 16%, rgba(255, 255, 255, 0) 0%);
		background-size: 22px 2px, 6px 2px;
		background-repeat: repeat-x;
		background-position: bottom; }
	&:hover span {
		color: #0051FF;
		background-image: linear-gradient(to right, darken(#008CFF, 10) 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, darken(#008CFF, 10) 16%, rgba(255, 255, 255, 0) 0%); }
	&_rubric {
		line-height: 1.35;
		span {
			background-image: linear-gradient(to right, #B3B3B3 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #B3B3B3 16%, rgba(255, 255, 255, 0) 0%); }
		&:hover {
			border-bottom-color: transparent!important; }
		&:hover span {
			background-image: linear-gradient(to right, #0051FF 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #0051FF 16%, rgba(255, 255, 255, 0) 0%); } } }

.link-sale {
	color: #FCBD7E;
	font-size: 20px;
	@include trs;
	@include md {
		font-size: 18px; }
	@include sm {
		font-size: 16px; }
	span {
		border-bottom: 2px dashed #FCBD7E; }
	&:hover span {
		color: darken(#FCBD7E, 15);
		border-bottom-color: darken(#FCBD7E, 15); } }

/* about text
	----------------------------------- */
.about-text {
	@include md {
		margin-bottom: 10px; }
	&__container {
		max-width: 1500px;
		@include md {
			max-width: 1200px; }
		@include sm {
			max-width: 970px; } }
	&__title {
		text-align: center;
		margin-bottom: 20px;
		font-size: 23px;
		@include lg {
			font-size: 21px; }
		@include md {
			font-size: 18px;
			margin-bottom: 10px; }
		@include sm {
			font-size: 16px; } }
	&__descr {
		line-height: (30/22);
		text-indent: 45px;
		font-size: 20px;
		@include lg {
			font-size: 19px; }
		@include md {
			font-size: 16px; }
		@include sm {
			font-size: 14px; } } }

// pic decor
.decor {
	position: relative;
	background-color: #F4F8FD;
	&::after {
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: 0;
		content: '';
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 0 20px 20px;
		border-color: transparent transparent #fff transparent; }
	&__line {
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 87%;
		height: 1px;
		background-color: rgba(#DAE5EC, .65);
		align-self: flex-end;
		&::before, &::after {
			position: absolute;
			z-index: 1;
			bottom: 0;
			content: '';
			transition: border-color .3s; }
		&::before {
			right: -16px;
			@include arr(15,20, rgba(#000, .25),br); }
		&::after {
			right: -39px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 22px 23px 0 0;
			border-color: rgba(238, 244, 251, 0.9) transparent transparent transparent;
			@include sm {
				right: -39px; } }
		&_suggest {
			width: 95%; } }
	&_sm {
		background-color: #D7DCE0;
		&::after {
			display: none; }
		.decor__line {
			width: 85%; }
		.decor__line:after {
			right: -20px;
			border-width: 19px 19px 0 0;
			background-color: #ececec; }
		.decor__line:before {
			right: -1px; } }

	&_xs {
		&::after {
			border-width: 0 0 20px 19px;
			right: -2px; }
		.decor__line {
			width: 80%;
			bottom: 0;
			@include md {
				width: 81%; } }
		.decor__line::after {
			right: -26px;
			border-width: 18px 18px 0 0; }
		.decor__line::before {
			right: -8px;
			border-width: 0 0 18px 13px; } } }

/* breadcrumbs
	----------------------------------- */
.breadcrumbs {
	&__list {
		font-size: 0; }
	&__item {
		display: inline-block;
		vertical-align: middle;
		&:last-child a::after {
			display: none; }
		&.is-active {
			pointer-events: none; } }
	&__link {
		font-size: 20px;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; }
		&::after {
			display: inline-block;
			vertical-align: middle;
			margin: 0 4px;
			content: '/';
			pointer-events: none; }
		&:hover {
			color: $hover; }
		&:hover::after {
			color: #000; } }
	span {
		display: inline-block;
		vertical-align: middle; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 8px;
		fill: #D0CFCF; } }

/* page
	----------------------------------- */
