			.icon-souvenirs {
				right: 16px;
				font-size: 25px; }
			.icon-books_page {
				right: 15px;
				font-size: 25px;
				@include md {
					font-size: 21px; }
				@include sm {
					font-size: 17px; } }
			.icon-presents {
				font-size: 28px;
				right: 14px;
				@include md {
					font-size: 24px; }
