/* pagination
	----------------------------------- */
.pagination {
	padding: 7px 0;
	background: #F7F9FC;
	font-size: 0;
	&__list {
		font-size: 0;
		text-align: center; }
	&__list-item {
		display: inline-block;
		vertical-align: middle;
		margin: 0 5px;
		&_prev {
			margin-right: 10px;
			text-align: center;
			.pagination__list-link {
				padding: 0;
				line-height: 39px;
				@include md {
					line-height: 34px; }
				@include sm {
					line-height: 29px; } } }
		&_dots {
			margin: 0 10px;
			@include md {
				margin: 0 5px; } } }
	&__list-item.is-active &__list-link {
			background: #FFEBAB; }
	&__list-link {
		display: inline-block;
		font-size: 19px;
		font-family: Arial, sans-serif;
		color: #667FA6;
		padding: 6px 12px;
		border-radius: 5px;
		height: 39px;
		min-width: 38px;
		@include md {
			padding: 6px 8px;
			height: 34px;
			min-width: 34px;
			font-size: 16px; }
		@include sm {
			padding: 4px;
			height: 29px;
			min-width: 31px;
			font-size: 14px; }
		&:hover {
			background: #EDEDED; }
		&:active {
			background: #C8D6E6; } }
	&__list-link:hover &__icon-arr {
		fill: lighten(#667FA6, 20); }
	&__icon-arr {
		display: inline-block;
		vertical-align: middle;
		fill: #EDEDED;
		font-size: 27px;
		@include md {
			font-size: 21px; }
		@include sm {
			font-size: 18px; } }

	&_product {
		margin: 20px 0 45px;
		@include md {
			margin: 10px 0 30px; }
		@include sm {
			margin: 10px 0 15px; } }
	&_watched {
		margin: 30px 0;
		background: none;
		@include md {
			margin: 15px 0; }
		@include sm {
			margin: 8px 0; } }
	&_news {
		margin-top: 25px; } }

/* sub-menu
	----------------------------------- */
.category-menu {
	position: relative;
	&::after {
		content: '';
		position: absolute;
		bottom: -1px;
		left: 5px;
		right: 5px;
		height: 1px;
		background: #D1D1D1;
		z-index: 1; }
	&__title {
		margin-bottom: 40px;
		text-align: center;
		color: #274F7F;
		@include md {
			margin-bottom: 25px; } }
	&__list {
		display: flex; }
	&__item {
		flex: 1 0 auto; }
	&__link {
		display: block;
		padding: 15px 5px;
		font-size: 19px;
		background: #F7FAFF;
		text-align: center;
		transition: background .15s ease;
		@include md {
			font-size: 15px; }
		@include sm {
			font-size: 14px; }
		&:hover {
			background: #E5F0FC; }
		&:active {
			background: darken(#E5F0FC, 5); } }

	&_default {
		margin-top: auto; }
	&_basket, &_order {
		max-width: 1400px;
		margin: 50px auto 0; } }

/* rating
	----------------------------------- */
.rating {
	&__text {
		margin-bottom: 3px;
		color: #1C487A;
		@include md {
			font-size: 14px; }
		@include sm {
			font-size: 15px; } }
	&__in {
		display: flex;
		flex-direction: row-reverse;
		justify-content: center; }
	&__input {
		display: none; }
	&__input ~ &__icon .rating__star {
		fill: #DBDBDB;
		stroke: #DBDBDB; }
	&__input:checked ~ &-icon .rating__star,
	&__input:checked &-icon .rating__star {
		fill: #FFED00;
		stroke: #FFED00; }
	&__icon:hover .rating__star,
	&__icon:hover ~ &__icon .rating__star {
		fill: #C7CBCF;
		stroke: #DBDBDB; }
	&__input:checked &__icon:hover .rating__star,
	&__input:checked ~ &__icon .rating__star {
		fill: #FFED00;
		stroke: #FFED00; }
	&__icon {
		display: inline-block;
		cursor: pointer; }
	&__star {
		font-size: 23px;
		@include md {
			font-size: 19px; } } }

/* check
	----------------------------------- */
.check {
	&__circle {
		display: inline-block;
		margin-left: 4px;
		width: 24px;
		height: 24px;
		border: 2px solid #99AFCF;
		border-radius: 50%;
		text-align: center;
		line-height: 20px;
		@include md {
			width: 20px;
			height: 20px;
			line-height: 14px; }
		@include sm {
			line-height: 16px; }
		&::after {
			display: inline-block;
			width: 14px;
			height: 14px;
			border-radius: 50%;
			background: #768FAE;
			content: '';
			transform: scale(.4);
			opacity: 0;
			transition: .15s;
			@include md {
				width: 10px;
				height: 10px; } } }
	label {
		font-size: 19px;
		color: #000;
		cursor: pointer;
		@include md {
			font-size: 17px; }
		@include sm {
			font-size: 15px; } }
	input {
		display: none; }
	input:checked + label .check__circle::after {
		transform: scale(1);
		opacity: 1; } }

/* label-block
	----------------------------------- */
.label-block {
	position: relative;
	&__title {
		position: absolute;
		top: 4px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
		padding: 10px;
		@include f('tcm', 16, 18);
		color: #004D8C;
		background: rgba(#DEF1FF, .6);
		text-transform: uppercase;
		@include md {
			padding: 6px;
			font-size: 15px; }
		@include sm {
			padding: 5px;
			font-size: 13px; } }
	&__in {
		padding: 4px;
		border: 2px solid #47C1F0;
		@include clr;
		@include sm {
			padding: 2px; } }
	&__item {
		position: relative;
		float: left;
		width: calc(50% - 5px);
		padding-top: 36%;
		margin-right: 5px;
		margin-bottom: 5px;
		@include clr;
		&:nth-child(4) {
			padding-top: 0; }
		&:nth-child(even) {
			width: 50%;
			margin-right: 0; }
		&-img {
			@include coverdiv;
			@include cover; }
		&_half {
			padding-top: 72%;
			margin-bottom: 0; }
		&_double {
			width: 50%;
			padding-top: 35%;
			margin-right: 0;
			&:nth-child(3) {
				margin-bottom: 0; } }
		&-label {
			position: absolute;
			left: 0;
			bottom: 0;
			padding: 10px;
			z-index: 1;
			@include f('oss', 16, 16);
			color: #fff;
			background: rgba(#000, .45);
			@include md {
				padding: 7px;
				font-size: 13px; }
			@include sm {
				padding: 5px;
				font-size: 11px; }
			&_top {
				top: 0;
				bottom: auto; } } }

	&__logo {
		padding: 7px 0;
		background: #47C1F0;
		&-img {
			width: 86%;
			margin: 0 auto; } } }

/* benefits-list
	----------------------------------- */
.benefits-list {
	&__title {
		font-size: 20px;
		text-align: center;
		font-weight: 700;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 15px; } }
	&__descr {
		margin-top: 35px;
		text-indent: 30px;
		@include md {
			margin-top: 25px; }
		@include sm {
			margin-top: 15px; } }
	ul {
		margin-top: 20px;
		@include md {
			margin-top: 10px; }
		li {
			text-indent: 30px; }
		li:not(:last-child) {
			margin-bottom: 20px;
			@include md {
				margin-bottom: 15px; } } }
	&__link {
		color: #274F7F;
		background: transparent;
		text-decoration: underline;
		text-decoration-color: #274F7F; }
	&_default {
		margin: 30px 0 0;
		@include md {
			margin: 20px 0 0; }
		@include sm {
			margin: 15px 0 0; } }
	&_centered &__title {
		padding-left: 120px;
		@include f('oss');
		font-size: 18px;
		text-align: left;
		@include md {
			padding-left: 80px;
			font-size: 16px; }
		@include sm {
			padding-left: 40px; } }
	&_centered ul {
		padding-left: 95px;
		@include md {
			padding-left: 50px; }
		@include sm {
			padding-left: 15px; } }
	&_centered ul li:not(:last-child) {
		margin-bottom: 3px!important; }
	&_centered &__descr {
		text-indent: 0; }
	&_centered &__descr:nth-of-type(2) {
		padding-left: 125px;
		@include md {
			padding-left: 90px; }
		@include sm {
			padding-left: 60px; } } }

/* section defaults
	----------------------------------- */
.sect {
	padding-bottom: 40px;
	@include md {
		padding-bottom: 30px; }
	@include sm {
		padding-bottom: 20px; } }
.sect-title {
	@include sm {
		padding: 0 4px;
		font-size: 16px; } }
.sect-descr {
	margin: 20px 0;
	text-indent: 30px;
	@include md {
		margin: 15px 0; }
	@include sm {
		margin: 10px 0; } }

/* zoom
	----------------------------------- */
.zoom {
	position: relative;
	&__element {
		display: block; }
	&__element:hover &__btn {
		opacity: 1; }
	&__img {
		width: 100%; }
	&__btn {
		position: absolute;
		left: -10px;
		bottom: -10px;
		padding: 10px;
		@include tr(opacity .3s);
		opacity: 0;
		z-index: 1;
		&:hover &-icon {
			fill: darken(#B9BDC7, 10); }
		&:active &-icon {
			fill: darken(#B9BDC7, 20); }
		&-icon {
			font-size: 36px;
			fill: #B9BDC7;
			@include sm {
				font-size: 25px; } }
		&_sm {
			padding: 0;
			left: 6px;
			bottom: 4px; }
		&_sm &-icon {
			font-size: 28px;
			fill: #fff;
			@include sm {
				font-size: 22px; } }
		&_top {
			top: 8px;
			bottom: auto; }
		&_color &-icon {
			fill: #5b5f69; }
		&_top-r {
			padding: 5px;
			top: 10px;
			right: 10px;
			bottom: auto;
			left: auto;
			@include sm {
				top: 5px;
				right: 5px; } }
		&_top-r &-icon {
			fill: #fff; }
		&_top-rc {
			padding: 5px;
			top: 10px;
			right: 10px;
			bottom: auto;
			left: auto; }
		&_top-rr &-icon {
			fill: #B3B3B3; } } }

// send

.send {
	&__add {
		display: inline-block;
		vertical-align: top;
		margin-right: 15px;
		white-space: nowrap;
		&-btn {
			display: inline-block;
			vertical-align: middle;
			padding: 5px 5px 0;
			max-width: 128px;
			min-height: 114px;
			color: #fff;
			background-color: #80C9E8;
			white-space: normal;
			@include f("ver",16);
			text-transform: uppercase;
			text-align: center;
			cursor: pointer;
			border-bottom: 1px solid #A8B3BF;
			&::after {
				content: '';
				display: block;
				width: 56px;
				height: 52px;
				margin: 3px auto 0;
				@include ico('file');
				@include md {
					width: 44px;
					height: 42px; } }
			&:hover::after {
				@include ico('file-hvr'); }
			&:active::after {
				@include ico('file-act'); }
			&:hover {
				background: #5EAFDA;
				border-bottom-color: #FFFF00; }
			&:active {
				background: #2A578A;
				border-bottom-color: #E63E2D; } }
		&-input {
			display: none; }
		&-text {
			display: inline-block;
			vertical-align: top;
			margin-left: 20px;
			margin-top: -3px;
			max-width: 160px;
			padding-left: 3px;
			@include f("ver",18);
			color: #040404;
			white-space: normal;
			transition: .4s;
			&.is-visible {
				opacity: 1;
				visibility: visible; }
			@include md {
				font-size: 15px; }
			@include sm {
				font-size: 14px; } } }
	&__files {
		display: inline-block;
		vertical-align: bottom;
		font-size: 0;
		white-space: normal;
		width: percentage(280 / 600); }
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		height: 116px;
		width: 130px;
		margin-right: 20px;
		@include vertical;
		@include md {
			width: 110px;
			height: 98px;
			margin-right: 15px; }
		&:last-child {
			margin-right: 0; }
		&:nth-child(2n) {
			margin-right: 0; }
		&:nth-child(2) ~ & {
			margin-top: 20px; }
		img {
			display: inline-block;
			vertical-align: middle;
			width: 100%;
			height: 100%;
			object-fit: contain; }
		.decor__line {
			@include md {
				width: 82%; } }
		.decor__line::before {
			@include md {
				border-width: 0 0 16px 15px;
				right: -5px; } }
		.decor__line::after {
			@include md {
				border-width: 15px 15px 0 0; } }

		&-cancel {
			position: absolute;
			right: -15px;
			top: -15px;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			background: #E63833;
			border: 3px solid #fff;
			@include tr(background .2s);
			@include md {
				width: 24px;
				height: 24px;
				border-width: 2px; }
			&:hover {
				background: #3e5b8a; }
			&-icon {
				fill: #fff;
				@include vcenter;
				font-size: 12px;
				@include md {
					font-size: 10px; } } } }

	&__message {
		text-align: center;
		&-text {
			display: inline-block;
			vertical-align: -13px;
			@include f("ver", 20);
			@include md {
				font-size: 15px; }
			@include sm {
				font-size: 14px; } }
		&-icon {
			display: inline-block;
			vertical-align: middle;
			font-size: 50px;
			fill: #aec94d; } }

	&_order {
		margin-top: 25px;
		font-size: 0; }
	&_order &__item {
		width: 100px;
		height: 90px;
		&:nth-child(2) {
			margin-right: 20px; }
		&:nth-child(2) ~ * {
			margin-right: 20px;
			margin-top: 0; }
		&:nth-child(6) ~ * {
			margin-top: 20px; }
		.decor__line {
			@include md {
				width: 83%; } }
		.decor__line:before {
			right: 0;
			border-width: 0 0 16px 15px; }
		.decor__line:after {
			right: -17px;
			border-width: 16px 17px 0 0;
			background-color: #E0E0E0; } }
	&_order &__item-cancel {
		right: -15px;
		top: -15px;
		width: 24px;
		height: 24px;
		border: 2px solid #fff; }

	&_order &__add {
		margin-right: 25px; }
	&_order &__add-btn {
		position: relative;
		display: block;
		width: 100px;
		height: 90px;
		min-height: 0;
		padding: 5px 0 0;
		color: #fff;
		text-transform: uppercase;
		text-align: center;
		background: #B9C0C9;
		border-bottom: none;
		@include f('ver', 13);
		&:hover {
			background: darken(#B9C0C9, 10); }
		&::after {
			left: calc(50% - 18px);
			width: 36px;
			height: 34px;
			bottom: 10px; } }
	&_order &__files {
		width: calc(100% - 125px); } }
