/* order
	----------------------------------- */
.order {
	padding: 25px 25px 35px;
	@include md {
		padding: 15px 20px 25px; }
	@include sm {
		padding: 15px 15px 20px; }
	&__in {
		max-width: 1300px;
		margin: 0 auto;
		@include clr; }
	&__col {
		float: left;
		padding: 0;
		&:nth-child(1) {
			width: 32%; }
		&:nth-child(2) {
			width: 68%;
			padding-left: 7%; } }
	&__row {
		clear: both;
		padding-top: 50px;
		text-align: center;
		@include md {
			padding-top: 30px; }
		@include sm {
			padding-top: 20px; } }
	&__title {
		margin-bottom: 35px;
		@include f('ver', 22);
		text-align: center;
		@include md {
			margin-bottom: 20px;
			font-size: 19px; }
		@include sm {
			margin-bottom: 15px;
			font-size: 16px; } }
	&__form-group {
		&-label {
			font-size: 22px;
			color: #999999;
			@include md {
				font-size: 19px; }
			@include sm {
				font-size: 17px; }
			& + & {
				margin-top: 10px; } }
		& + & {
			margin-top: 25px;
			@include md {
				margin-top: 20px; }
			@include sm {
				margin-top: 15px; } } }
	&__form-field {
		position: relative;
		padding-left: 50px;
		border: 2px solid #99AFCF;
		overflow: hidden;
		&::before {
			pointer-events: none;
			position: absolute;
			top: -3px;
			left: -1px;
			width: 54px;
			height: 45px;
			content: '';
			@include md {
				width: 50px;
				height: 39px; } }
		&_name::before {
			@include ico('user-log'); }
		&_email::before {
			@include ico('email-log'); }
		&_pass::before {
			@include ico('dots-log'); }
		&_tel::before {
			@include ico('phone-log'); }
		&_text {
			padding: 0 15px;
			text-align: center;
			border: none; }
		&_required {
			position: relative;
			&::after {
				position: absolute;
				right: 2px;
				top: -2px;
				content: '*';
				color: #E63833;
				@include f('ver', 22);
				@include sm {
					top: 0;
					right: 4px;
					font-size: 17px; } } }
		&_address {
			padding: 0;
			textarea {
				display: block;
				width: 100%;
				min-height: 120px;
				padding: 10px 15px;
				resize: none;
				border: none;
				@include f('ver', 19);
				@include md {
					font-size: 17px; }
				@include md {
					font-size: 15px; } } }
		& + & {
			margin-top: 15px; } }

	&__form-ask {
		margin: 20px 0;
		text-align: center;
		font-size: 19px;
		@include md {
			font-size: 17px; }
		@include sm {
			margin: 15px 0;
			font-size: 15px; }
		&-in {
			margin-top: 15px;
			font-size: 0;
			.order__check {
				margin: 0 15px; } } }
	&__input {
		width: 100%;
		line-height: 40px;
		height: 40px;
		padding: 0 20px 0 15px;
		@include f('ver', 19);
		color: #000;
		@include placeholder-color(#999999);
		@include md {
			height: 34px;
			padding: 0 20px 0 10px;
			line-height: 34px;
			font-size: 16px; }
		@include sm {
			font-size: 14px; } }

	&__check {
		display: inline-block; }
	&__comment {
		margin-top: -5px;
		&-in {
			padding: 20px;
			background: #E0E0E0;
			@include sm {
				padding: 15px; } }
		&-descr {
			margin-bottom: 10px;
			@include sm {
				font-size: 15px; } }
		&-message-wrap {
			position: relative;
			padding-left: 53px;
			border: 2px solid #99AFCF;
			background: #F7F7F7;
			overflow: hidden;
			&::before {
				content: '';
				position: absolute;
				top: -1px;
				left: 0;
				width: 52px;
				height: 40px;
				@include ico('dots-log'); } }
		&-message {
			display: block;
			width: 100%;
			padding: 6px 10px 30px;
			resize: none;
			min-height: 170px;
			border: none;
			@include f('psr', 19);
			transition: min-height 0.3s;
			@include sm {
				font-size: 17px; }
			&.extended {
				min-height: 220px; } }
		&-extend {
			position: absolute;
			left: calc(50% - 33px);
			bottom: 3px;
			width: 66px;
			height: 27px;
			padding-top: 5px;
			background: #F7F7F7;
			&:hover {
				background: #E0E0E0; }
			&:hover &-icon {
				fill: #56708A; }
			&:active &-icon {
				fill: #103559; }
			&-icon {
				fill: #C4C4C4;
				font-size: 17px; }
			&.is-active &-icon {
				transform: scaleY(-1); } } } }

/* basket
	----------------------------------- */
.basket {
	&__orders {
		border: 9px solid #DFDFDF;
		border-top-width: 5px;
		&-row {
			padding: 7px 0;
			border-bottom: 5px solid #DFDFDF;
			font-size: 0;
			color: #4D4D4D; }
		&-label {
			display: inline-block;
			vertical-align: top;
			padding: 0 15px;
			font-size: 19px;
			text-align: center;
			@include md {
				font-size: 17px; }
			@include sm {
				font-size: 15px; }
			&:nth-child(1) {
				width: 18%;
				@include md {
					width: 17%; } }
			&:nth-child(2) {
				width: 15%;
				@include md {
					width: 17%; } }
			&:nth-child(3) {
				width: 45%;
				@include md {
					width: 43%; }
				@include sm {
					width: 42%; } }
			&:nth-child(4) {
				width: 18%;
				@include md {
					width: 21%; }
				@include sm {
					width: 23%; } } }
		&-status {
			padding: 0 15px;
			font-size: 22px;
			text-align: center;
			@include md {
				font-size: 19px; }
			@include sm {
				font-size: 17px; }
			&-count {
				display: inline-block;
				@include f('oss'); } } }

	&__summ {
		padding: 30px 30px 40px;
		font-size: 23px;
		text-align: center;
		@include md {
			padding: 20px 20px 30px;
			font-size: 20px; }
		@include sm {
			padding: 15px;
			font-size: 17px; }
		&-text {
			color: #00306B;
			text-transform: uppercase; }
		&-price, &-words, &-delivery {
			display: inline-block; }
		&-price {
			margin: 0 15px;
			@include f('psb');
			@include md {
				margin: 0 8px; }
			.icon-ruble {
				fill: #00306B;
				font-size: 17px;
				@include md {
					font-size: 14px; } } }

		&-words, &-delivery {
			text-transform: none; }
		&-delivery {
			color: #808080; } }

	&__promo {
		margin: 20px 0 30px;
		@include md {
			margin: 5px 0 25px; }
		@include sm {
			margin: 5px 0 15px; }
		&-text {
			color: #B3B3B3; }
		&-input-wrap {
			max-width: 270px;
			margin: 3px auto 0;
			border: 1px solid #99AFCF;
			input {
				width: 100%;
				height: 45px;
				line-height: 45px;
				padding: 0 10px;
				@include f('ver', 22);
				@include md {
					height: 34px;
					line-height: 34px;
					font-size: 19px; }
				@include sm {
					font-size: 16px; } } } } }
