				left: 0;
				bottom: 137px;
				@include md {
					bottom: 109px; }
				@include sm {
					bottom: 88px; } }
			&_4 {
				top: 32.4%;
				right: 1px;
				@include md {
					top: 31.4%; }
				@include sm {
					top: 27.9%; } }
			&_5 {
				top: 57.3%;
				left: 1px;
				@include md {
					top: 55.9%; }
				@include sm {
					top: 54.2%; } }
			&_6 {
				top: 57.3%;
				right: 1px;
				@include md {
					top: 55.8%; }
				@include sm {
					top: 54%; } }
			&_7 {
				top: 77.1%;
				right: 1px;
				@include md {
					top: 75.7%; }
				@include sm {
					top: 74%; } }
			&_8 {
				top: 55.2%;
				right: 1px;
				@include md {
					top: 54.1%; }
				@include sm {
					top: 52.4%; } }
			&_9 {
				top: 194px;
				left: 16px;
				background: #fff;
				border: 1px solid #7e71b1;
				height: 40px;
				line-height: 28px;
				@include md {
					top: 178px;
					left: 6px;
					height: 38px;
					line-height: 26px; }
				@include sm {
					top: 142px;
					left: 0px;
					height: 30px;
					line-height: 19px; } }
			&_10 {
				top: 53.4%;
				left: 0;
				@include md {
					top: 52%; } }
			&_11 {
				top: 37.2%;
				right: 0; } }
		&_lg {
