				transform: translateY(-46%); }
			.product-types__item-in {
				top: 46%; }
			.product-types__item-text {
				margin-top: 267px;
				background: rgba(0, 0, 0, 0.6);
				@include md {
					margin-top: 230px; }
				@include sm {
					margin-top: 138px; } }
			.product-types__item-title {
				padding: 34px 10px;
				font-size: 22px;
				@include md {
					padding: 20px 5px;
					font-size: 17px; }
				@include sm {
					padding: 22px 0;
					font-size: 15px; } }
			.product-types__item-title span {
				border-bottom: none; }
			.product-types__item-label {
				color: #fff;
				background: rgba(#757776, .8); } }
		&-in {
			position: absolute;
			top: 56%;
			width: 100%;
			height: 100%;
			@include gpu;
			transition: background .4s ease, transform .4s ease;
			will-change: transform; }
		&-text {
			margin-top: 140px;
			transition: background .4s ease;
			background: rgba(0, 0, 0, 0.4);
			@include f('oss', 18, 18);
			color: #fff;
			text-align: center;
			@include md {
				margin-top: 110px;
				font-size: 14px; }
			@include sm {
				margin-top: 80px; } }
		&-title {
			padding: 17px 10px;
			text-transform: uppercase;
			font-weight: 600;
			@include sm {
				padding: 17px 2px;
				font-size: 13px; }
			span {
				border-bottom: 1px solid transparent; } }
		&-descr {
			padding: 0 27px;
			line-height: 1.4;
