			@include sm {
				padding: 0 10px; } }
		&_border-blue {
			border-bottom-color: #1C8AFF; }
		&_border-red {
			border-bottom-color: #FF7A7A; }
		&_border-yellow {
			border-bottom-color: #FFFF47; }
		&_border-green {
			border-bottom-color: #5CDB86; }
		&_border-orange {
			border-bottom-color: #f9c565; }
		&_border-purple {
			border-bottom-color: #f7adff; } }
	&_cups {
		max-width: 1140px;
		margin: 0 auto 50px;
		@include md {
			margin: 0 auto 25px; } }
	&_cups-2 {
		margin-bottom: 60px;
		@include md {
			margin-bottom: 30px; } } }

/* look
	----------------------------------- */
.look {
	&__row {
		@include justify; }
	&__item {
		display: inline-block;
		vertical-align: middle;
		&:nth-child(1) {
			width: percentage(279 / 960); }
		&:nth-child(2) {
			width: percentage(594 / 960); }
