			margin: 25px auto; } } }

/* send-now
	----------------------------------- */
.send-now {
	display: flex;
	&__text {
		width: 73.3%;
		padding: 15px;
		background: #fff7c5;
		font-family: Arial, sans-serif;
		&-mail {
			color: #0000c4;
			text-decoration: underline;
			text-decoration-color: #0000c4; } }
	&__actions {
		width: 26.7%;
		&-btn {
			display: block;
			height: 50%;
			border-bottom: 2px solid #fbff00;
			@include vertical;
			transition: .15s ease;
			cursor: pointer;
			span {
				display: inline-block;
				vertical-align: middle;
				font-size: 18px;
				text-transform: uppercase;
				@include md {
					font-size: 16px; } }
			&_top {
				background: #FDD17D;
				&:hover {
					background: #FEE1AD;
					border-bottom-color: #ffff99; }
				&:active {
					background: #C39767;
					border-bottom-color: #897E91; } }
			&_bottom {
				background: #F4AF67;
				&:hover {
					background: #FDE0BD;
					border-bottom-color: #9CD9FF; }
				&:active {
					background: #C39767;
					border-bottom-color: #584466; } }
			input[type='file'] {
				display: none; } } }
	&_cups {
		max-width: 1050px;
		margin: 0 auto; } }

/* price-table
	----------------------------------- */
.price-table {
	&__title {
		position: relative;
		margin-bottom: 20px;
		font-family: Arial, sans-serif;
		font-size: 19px;
		font-weight: 700;
		text-align: center;
		@include sm {
			margin-bottom: 13px;
			font-size: 16px; }
		&::after {
			@include vcenter;
			width: 94%;
			height: 1px;
			background: #cccccc;
			z-index: 1;
			content: ''; }
		span {
			position: relative;
			padding: 0 15px;
			z-index: 2;
			background: #fff; } }
	&__table {
		text-align: center;
		font-size: 19px;
		font-family: Arial, sans-serif;
		line-height: 1.2;
		@include md {
			font-size: 15px; }
		@include sm {
			font-size: 14px; }
		td {
			vertical-align: middle;
			width: 11%;
			padding: 5px;
			border: 2px solid #d7d7d7;
			&:nth-child(1) {
				width: 27%; } } }

	&__head {
		tr:nth-child(1) {
			@include f('psr', 20);
			background: #73d5ff;
			@include md {
				font-size: 18px; }
			@include sm {
				font-size: 16px; } }
		tr:nth-child(2) {
			background: #9cfcca; }
		td {
			padding: 10px 5px;
			border-color: #fff; }
		tr:nth-child(2) td {
			box-shadow: 0px 3px 0px #d7d7d7; }
		mark {
			margin: 0 10px;
			padding: 0 20px;
			background: #ed0000;
			color: #fff;
			@include f('psb', 21);
			@include md {
				font-size: 18px; }
			@include sm {
				font-size: 15px; } }
		&_color tr:first-child {
			color: #ffffff; } }
	&__text {
		display: block;
		margin: 10px 0;
		&_highlight {
			color: #ed0000; } }
	&__label {
		max-width: 310px;
		margin: 0 auto 10px;
		display: block;
		padding: 5px;
		background: #ebf8ff;
		&_color {
			background: #fff9c4; }
		&_width {
			max-width: 920px;
			margin: 0 auto 10px; }
		&_sm {
			max-width: 274px; }
		&_full {
			max-width: 333px; } }
	&__link {
		display: inline-block;
		margin: 20px 0 10px;
		font-family: Arial, sans-serif;
		color: #003399;
		@include sm {
			margin: 8px 0 3px; }
		&:hover {
			text-decoration: underline;
			text-decoration-color: currentColor; } }
	&__view {
		font-size: 0;
		&_padding {
			padding: 0 1px; }
		&-item {
			display: inline-block;
			vertical-align: middle;
			&_half {
				width: 50%; } }

		&_white-c &-item {
			width: percentage(90 / 342);
			&:nth-child(2) {
				width: percentage(144 / 342); } }
		&_chameleon-c &-item {
			width: percentage(65 / 342); }
		&_interesting-c &-item {
			margin: 0 0.16% !important;
			&:nth-child(1) {
				width: percentage(149/ 1210); }
			&:nth-child(2) {
				width: percentage(137/ 1210); }
			&:nth-child(3), &:nth-child(4) {
				width: percentage(259/ 1210); }
			&:nth-child(5) {
				width: percentage(151/ 1210); }
			&:nth-child(6) {
				width: percentage(171/ 1210); } }
		&_paper-packs {
			padding: 12px 0;
			.zoom__btn {
				left: auto;
				right: -10px;
				top: 5px; } }
		&_love-pakcs &-item {
			width: percentage(102 / 342); }
		&_spaces &-item {
			margin: 0 percentage(2 / 342); }
		&_spaces-sm &-item {
			margin: 0 percentage(1 / 342); }
		&_spaces-lg &-item {
			padding: 0 12px;
			margin: 0; }
		&-img {
			margin: 0 auto;
			width: 100%;
			&_width {
				width: auto; } }
		&-text {
			position: absolute;
			bottom: 0;
			left: 0;
			padding: 5px;
			@include f('tcm', 14);
			color: #666666;
			background: rgba(#fff, .7);
			text-align: left;
			@include md {
				font-size: 12px; }
			@include sm {
				font-size: 9px; }
			&_color {
				color: #66FFCC;
				background: rgba(#fff, .3); } } } }

/* teaser
	----------------------------------- */
.teaser {
	&__slider {
		display: none;
		&.slick-initialized {
			display: block; }
		&-item {
			padding: 0 2px;
			&:nth-child(4n) .zoom__btn_sld {
				right: auto;
				left: 10px; } }
		&-item:hover &-zoom {
			opacity: 1;
			visibility: visible; }
		&-item-pic {
			position: relative;
			padding-top: 88%; }
		&-img {
			@include coverdiv;
			@include cover; }
		&-zoom {
			opacity: 0;
			visibility: hidden;
			position: absolute;
			z-index: 3;
			top: 10px;
			right: 10px;
			@include trs;
			&-icon {
				font-size: 36px;
				fill: #fff;
				@include trs; }
			&:hover &-icon {
				fill: #4D4D4D; } }
		&-btn {
			position: absolute;
			top: 0;
			background: rgba(#000, .15);
			z-index: 2;
			width: 55px;
			height: 100%;
			@include md {
				width: 40px; }
			&_prev {
				left: 2px; }
			&_next {
				right: 2px; } }
		&-btn.slick-disabled &-icon {
			opacity: 0;
			visibility: hidden; }
		&-icon {
			fill: #fff;
			font-size: 45px;
			@include md {
				font-size: 34px; } }
		&-label {
			position: absolute;
			bottom: 0;
			z-index: 3;
			padding: 5px 10px;
			background: rgba(#fff, .6);
			color: #4D4D4D;
			@include f('tcm', 14);
			line-height: 1.2;
			@include md {
				font-size: 12px; }
			@include sm {
				padding: 5px;
				font-size: 11px; }
			&_right {
				right: 0;
				text-align: right; } } } }

.our-address {
