.delivery {
	margin-bottom: 60px;
	@include md {
		margin-bottom: 10px; }
	&__title {
		position: relative;
		padding-top: 15px;
		text-align: center;
		z-index: 2;
		@include sm {
			padding-top: 5px; } }
	&__country {
		margin-bottom: 35px;
		@include md {
			margin-bottom: 20px; }
		&-in {
			position: relative;
			margin: 30px 0 0;
			@include md {
				margin: 20px 0 0; } }
		&-text {
			position: absolute;
			top: 0;
			left: 60px;
			z-index: 1;
			@include f('asd', 25);
			color: #1C487A;
			line-height: 1;
			@include md {
				font-size: 22px; } }
		&-pic {
			padding-top: 40px;
			img {
				margin: 0 auto;
				@include md {
					width: 100%; } } } }
	&__city {
		position: relative;
		min-height: 538px;
		@include md {
			min-height: 415px; }
		@include sm {
			min-height: 330px; }
		&-bg {
			@include coverdiv;
			@include cover;
			background-size: contain;
			z-index: 1; }
		&-text {
			position: relative;
			max-width: 520px;
			margin: 35px 0 0 60px;
			z-index: 2;
			@include md {
				margin: 25px 0 0 40px; }
			p + p {
				margin-top: 10px;
				@include sm {
					margin-top: 6px; } } } } }
