/* pictures
	----------------------------------- */
.pictures {
	padding-bottom: 30px;
	@include sm {
		padding-bottom: 20px; }
	&__title {
		padding: 0 100px;
		text-align: center;
		@include md {
			padding: 0 30px; } }
	&__descr {
		margin: 20px 0;
		text-indent: 40px; }
	&__pics {
		@include clr;
		&-item {
			position: relative;
			float: left;
			padding-top: percentage(344 / 1224);
			&:not(:first-child) {
				margin-left: 0.5%;
				@include md {
					margin-left: 0.2%; } }
			&:nth-child(1) {
				width: 22.1%; }
			&:nth-child(2) {
				width: 38.3%; }
			&:nth-child(3) {
				width: 38.4%; }
			img {
				@include coverdiv;
				@include cover;
				z-index: 1; } }
		&-label {
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 1;
			padding: 10px 15px;
			color: #fff;
			font-size: 20px;
			background: rgba(#000, .3);
			text-transform: uppercase;
			@include md {
				padding: 5px 10px;
				font-size: 17px; }
			@include sm {
				font-size: 14px; }
			&_right {
				right: 0;
				left: auto; } } } }

/* clothes
	----------------------------------- */
.clothes {
	padding: 0 0 40px;
	@include md {
		padding: 0 0 15px; }
	&__in {
		padding: 20px 0 0;
		@include clr;
		@include sm {
			padding: 10px 0 0; } }
	&__col {
		float: left;
		&:nth-child(1) {
			width: 55%;
			padding-right: 20px; }
		&:nth-child(2) {
			width: 45%; } }
	&__sect-descr {
		&:first-child {
			margin-top: 0; } }
	&__label-block {
		width: 45%; } }

/* news
	----------------------------------- */
.news {
	&__post {
		padding-bottom: 9px;
		border-bottom: 1px solid #D6D9DC;
		@include clr;
		& + & {
			margin-top: 30px;
			@include md {
				margin-top: 20px; } }
		&-pic {
			float: left;
			width: 51%;
			padding-right: 2%;
			margin-bottom: -5px;
			@include sm {
				padding-right: 1%; } }
		&-img-wrap {
			img {
				width: 100%;
				height: 100%; } }
		&-date {
			padding: 7px 2px;
			@include f('oss', 16);
			color: #BCBFC2;
			@include md {
				font-size: 14px; }
			@include sm {
				font-size: 13px; }
			span + span {
				margin-left: 5px; } }
		&-title {
			float: right;
			width: 49%;
			padding: 2px 15px;
			margin-bottom: 15px;
			@include f('oss', 20);
			background: #F4F8FD;
			@include md {
				margin-bottom: 10px;
				font-size: 17px; }
			@include sm {
				padding: 2px 10px;
				font-size: 14px; } }
		&-descr {
			position: relative;
			float: left;
			padding: 0 0 30px;
			width: 49%;
			min-height: 265px;
			@include sm {
				min-height: 0; }
			&.is-open {
				float: none;
				width: auto; }
			p:first-child {
				text-indent: 30px; }
			p:nth-child(2) {
				display: none; }
			&_important {
				font-weight: bold; } }
		&-descr.is-open.news__post-descr_important {
			border-color: transparent; }
		&-more-btn {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: 4px;
			z-index: 1;
			font-size: 18px;
			color: #004D87;
			@include md {
				font-size: 16px; }
			@include sm {
				font-size: 14px; }
			span {
				display: inline-block;
				vertical-align: middle; }
			&.is-active {
				left: 75%; }
			&.is-active .news__post-more-icon {
				@include rotate(180); } }
		&-more-icon {
			display: inline-block;
			vertical-align: middle;
			font-size: 10px;
			fill: #004D87;
			@include md {
				font-size: 8px; }
			@include sm {
				font-size: 6px; } } } }

/* machine
	----------------------------------- */
.machine {
	&__in {
		padding: 20px 0;
		@include clr;
		@include sm {
			padding: 10px 0; } }
	&__title {
		@include md {
			padding: 0 14px; }
		@include sm {
			padding: 0 10px;
			font-size: 16px; } }
	&__text {
		p {
			text-indent: 30px; }
		p + p {
			margin-top: 20px; } }
	&__pic {
		float: right;
		width: percentage(490 / 1224);
		margin-left: 20px;
		img {
			width: 100%; } }
	&__content {
		position: relative;
		clear: both;
		padding: 30px 0;
		@include md {
			padding: 20px 0; }
		@include sm {
			padding: 12px 0; }
		&::after {
			content: '';
			display: block;
			height: 114px;
			width: 100%;
			background: url(../img/bg/scissors-decor.svg) no-repeat 50% 50%/cover;
			@include md {
				height: 84px; }
			@include sm {
				height: 69px; } }
		&.is-open {
			margin-bottom: 20px; }
		&-in {
			display: none;
			max-width: 860px;
			margin: 0 auto 30px;
			@include sm {
				margin: 0 auto 20px; } }
		&-title {
			text-indent: 50px; }
		&-list {
			margin-top: 30px;
			@include md {
				margin-top: 20px; }
			@include sm {
				margin-top: 13px; }
			li {
				text-indent: 20px; }
			li:not(:last-child) {
				margin-bottom: 20px;
				@include md {
					margin-bottom: 15px; }
				@include sm {
					margin-bottom: 10px; } } } }
	&__more-content-open {
		display: inline;
		vertical-align: top;
		margin-left: 4px; }
	&__more-content-close {
		position: absolute;
		left: 50%;
		margin-left: -108px;
		bottom: -20px; } }

/*  payment
	----------------------------------- */
.payment {
	margin: 0 0 50px;
	&__in {
		padding: 20px 0 5px;
		background: #FAFEFF;
		@include clr;
		@include sm {
			padding: 10px 0 5px; } }
	&__col {
		padding: 0 4%;
		@include sm {
			padding: 0 3%; }
		&:nth-child(1) {
			float: left;
			width: 48%; }
		&:nth-child(2) {
			float: right;
			width: 37%; } }
	&__title {
		margin-bottom: 20px;
		color: #000;
		@include md {
			margin-bottom: 15px; }
		@include sm {
			margin-bottom: 8px; } }
	&__descr {
		font-size: 19px;
		color: #1A2F4A;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } }
	&__list {
		@include f('oss', 21);
		@include md {
			font-size: 18px; }
		@include sm {
			padding-top: 20px;
			font-size: 16px; }
		li {
			position: relative;
			padding-left: 40px;
			@include md {
				padding-left: 35px; }
			@include sm {
				padding-left: 30px; }
			&:not(:last-child) {
				margin-bottom: 5px; } }
		&-icon {
			position: absolute;
			top: -3px;
			left: 0;
			z-index: 1;
			fill: #AEC94D;
			font-size: 32px;
			@include md {
				font-size: 29px; }
			@include sm {
				font-size: 26px; } } }
	&__bg {
		position: relative;
		min-height: 520px;
		background: #FAFEFF;
		@include md {
			min-height: 400px; }
		@include sm {
			min-height: 360px; }
		&-img {
			@include coverdiv;
			@include cover; } } }
