body {
	min-width: 1000px;
	@include f("psr", 18, 26);
	color: $black;
	-webkit-font-smoothing: subpixel-antialiased;
	@include md {
		font-size: 16px; }
	@include sm {
		font-size: 14px; } }

// layout
.out {
	overflow: hidden; }
.container {
	max-width: 1560px;
	margin: 0 auto;
	padding: 0 15px;
	@include md {
		max-width: 1200px; }
	@include sm {
		max-width: 980px;
		padding: 0 10px; } }

// titles
h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

.title-xs {
	@include f("oss",19,30);
	color: $black; }

.title-sm {
	@include f("psb",20); }

.title-md {
	padding: 0 30px;
	@include f('psr', 21);
	line-height: 1.25;
	color: #00306B;
	text-transform: uppercase;
	&__color {
		color: #E63833; }
	@include md {
		font-size: 18px; }
	@include sm {
		padding: 0 8px; } }

.title-lg {
	@include f('psr',27);
	color: #00306B;
	text-transform: uppercase;
	@include md {
		font-size: 23px; }
	@include sm {
		font-size: 20px; } }

/* buttons
	----------------------------------- */
.btn {
	position: relative;
	display: inline-block;
	min-width: 340px;
	padding: 16px 70px 16px 5px;
	@include f("psr",22);
	color: $white;
	outline: none;
	text-decoration: none;
	text-align: center;
	background-color: #52C0E9;
	border-bottom: 1px solid #E7E7E7;
	@include md {
		min-width: 238px;
		padding: 11px 41px 11px 5px;
		font-size: 17px; }
	@include sm {
		min-width: 210px;
		padding: 6px 34px 6px 5px;
		font-size: 15px; }
	&::after {
		content: '';
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 32px;
		z-index: 1;
		width: 32px;
		height: 32px;
		@include ico('btn_arr');
		@include md {
			width: 26px;
			right: 10px; }
		@include sm {
			width: 21px;
			height: 20px; } }
	&:hover {
		background-color: #128FCD;
		border-bottom-color: #F9BF84; }
	&:hover::after {
		@include ico('btn_arr-hvr'); }
	&:active {
		background-color: #24568D;
		border-bottom-color: #E63833; }
	&:active::after {
		@include ico('btn_arr-act'); }
	&_dark {
		background-color: #1768BF;
		border-bottom-color: #F9BF84;
		&::after {
			@include ico('btn_arr_dark'); }
		&:hover {
			background-color: #00BBFF;
			border-bottom-color: #F9BF84; }
		&:hover::after {
			@include ico('btn_arr_dark-hvr'); }
		&:active {
			background-color: #24568D;
			border-bottom-color: #E63833; }
		&:active::after {
			@include ico('btn_arr-act'); } } }

.send-btn {
	position: relative;
	padding: 0 70px 0 5px;
	min-width: 232px;
	line-height: 45px;
	height: 45px;
	color: #fff;
	background: #6ca5db;
	text-align: right;
	border-bottom: 2px solid #a7b8c9;
	@include f("ver",19);
	overflow: hidden;
	&::after {
		position: absolute;
		top: 0;
		right: 0;
		width: 43px;
		height: 43px;
		@include ico('send');
		content: '';
		@include md {
			height: 36px;
			width: 36px; } }
	&:hover {
		background-color: #63afed;
		border-bottom-color: #ffff74; }
	&:hover::after {
		@include ico('send-hvr'); }
	&:active {
		background-color: #415c89;
		border-bottom-color: #a95a63; }
	&:active::after {
		@include ico('send-act'); } }

.btn-cart {
	position: relative;
	padding: 17px 90px 17px 50px;
	font-size: 21px;
	color: #fff;
	background: #2AB1E2;
	border-bottom: 2px solid #E3E3E3;
	overflow: hidden;
	@include md {
		padding: 15px 80px 15px 30px;
		font-size: 17px; }
	@include sm {
		padding: 11px 60px 11px 26px;
		font-size: 15px; }
	&::after {
		position: absolute;
		top: -1px;
		right: -1px;
		width: 64px;
		height: 63px;
		@include ico('cart_add-lg');
		content: '';
		@include md {
			width: 54px;
			height: 54px; }
		@include sm {
			width: 43px;
			height: 43px; } }
	&:hover {
		background: #01B6F8;
		border-bottom-color: #FFBE77; }
	&:hover::after {
		@include ico('cart_add-lg-hvr'); }
	&:active {
		background: #23568A;
		border-bottom-color: #E63E2D; }
	&:active::after {
		@include ico('cart_add-lg-act'); } }

.wish-btn {
	position: relative;
	min-width: 230px;
	height: 37px;
	line-height: 37px;
	padding: 0 40px 0 10px;
	text-align: center;
	font-size: 19px;
	color: #00306B;
	background: #EEF5FC;
	border-bottom: 1px solid #E1EFF6;
	@include md {
		font-size: 17px; }
	@include sm {
		font-size: 16px; }
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 36px;
		height: 36px;
		@include ico('fav'); }
	&:hover {
		background: #61CDFF;
		border-bottom-color: #FFC486;
		color: #fff; }
	&:active {
		background: #1E4D80;
		border-bottom-color: #FF0000;
		color: #fff; }
	&:hover::after {
		@include ico('fav-hvr'); }
	&:active::after {
		@include ico('fav-act'); } }

.order-btn {
	position: relative;
	min-width: 380px;
	height: 75px;
	line-height: 75px;
	padding: 0 76px 0 10px;
	text-align: center;
	font-size: 25px;
	color: #fff;
	background: #28B0E2;
	border-bottom: 2px solid #E0E0E1;
	overflow: hidden;
	text-transform: uppercase;
	@include md {
		min-width: 270px;
		height: 54px;
		padding: 0 55px 0 10px;
		line-height: 54px;
		font-size: 19px; }
	@include sm {
		height: 44px;
		padding: 0 33px 0 10px;
		line-height: 44px;
		font-size: 17px; }
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 73px;
		height: 73px;
		@include ico('like');
		@include md {
			height: 54px;
			width: 54px; }
		@include sm {
			height: 44px;
			width: 44px; } }
	&:hover {
		background: #06B2F0;
		border-bottom-color: #FFBE77; }
	&:active {
		background: #23568B;
		border-bottom-color: #FF0000; }
	&:hover::after {
		@include ico('like-hvr'); }
	&:active::after {
		@include ico('like-act'); } }

.confirm-btn {
	position: relative;
	min-width: 380px;
	height: 75px;
	line-height: 75px;
	padding: 0 76px 0 10px;
	text-align: center;
	font-size: 25px;
	color: #fff;
	background: #28B0E2;
	border-bottom: 2px solid #E0E0E1;
	overflow: hidden;
	text-transform: uppercase;
	@include md {
		min-width: 320px;
		padding: 0 54px 0 10px;
		height: 54px;
		line-height: 54px;
		font-size: 21px; }
	@include sm {
		height: 44px;
		min-width: 270px;
		padding: 0 27px 0 10px;
		line-height: 44px;
		font-size: 17px; }
	span {
		border-radius: 2px;
		border-bottom: 2px solid #2B99CC; }
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		width: 74px;
		height: 73px;
		@include ico('confirm');
		@include md {
			height: 52px;
			width: 53px; }
		@include sm {
			height: 42px;
			width: 43px; } }
	&:hover {
		background: #06B2F0;
		border-bottom-color: #FFBE77;
		span {
			border-bottom-color: #079DE3; } }
	&:active {
		background: #23568B;
		border-bottom-color: #FF0000;
		span {
			border-bottom-color: #254478; } }
	&:hover::after {
		@include ico('confirm-hvr'); }
	&:active::after {
		@include ico('confirm-act'); } }

.more-content-open {
	font-size: 18px;
	color: #004D87;
	@include md {
		font-size: 16px; }
	@include sm {
		font-size: 14px; }
	span {
		display: inline-block;
		vertical-align: middle; }
	&__icon {
		display: inline-block;
		vertical-align: -1px;
		margin-left: 4px;
		font-size: 8px;
		fill: #004D87;
		@include md {
			font-size: 6px; }
		@include sm {
			font-size: 5px; } } }
.more-content-close {
	display: none;
	padding-top: 9px;
	border-top: 1px solid #DFDFE0;
	width: 216px;
	&__icon {
		font-size: 16px;
		fill: #C2C2C2;
		@include rotate(180);
		@include md {
			font-size: 12px; } } }

// basic transition
@mixin trs {
	transition: .2s cubic-bezier(0.55, 0.09, 0.68, 0.53); }

// mini reset
a {
	text-decoration: none;
	color: inherit; }

img {
	display: block; }

button {
	background: none;
	@include f('psr'); }

